<template>
	<div class="share">
		<div class="box">
			<div class="content">
				<p class="title">哎哟,品味不错嘛</p>
				<p class="mag">
					<span class="dear">赶紧分享给亲爱的TA</span>
					<span class="test">测试下你们的感情默契读吧</span>
				</p>
				<p class="gauge" @click='invited'>邀请TA来测试</p>
				<p class="gauge" @click="privity">查看我的默契列表</p>
			</div>
		</div>
	</div>	
</template>
<script>
	export default{
		nane:'share',
		data(){
			return{

			}
		},
		methods: {
			//不需要存在的属性
			invited(){
				this.$router.push({
					path:'invitedhome'
				})
			},
			privity(){
				this.$router.push({
					path:'privity'
				})
			}
		}
	}
</script>
<style scoped lang="less">
@import "./../style/mixin.less";
	.share{
		position: absolute;
		left: 0;
		top: 0;
		width: 750*@rem; height: 1208*@rem; 
		background: url(../assets/rule-bg.png) no-repeat;
		background-size: 100% 100%;
		.box{
			width: 690*@rem; height: 900*@rem;
			background: url(../assets/share-box.png) no-repeat;
			background-size: 100% 100%;
			margin:50*@rem auto 0 ;
			.content{
				position: absolute;
				left:70*@rem;
				width: 610*@rem;margin-top: 210*@rem;
				color: #ff278d;

				.title{font-size: 55*@rem;font-weight: bold; text-align: center; margin-top: 85*@rem;}
				.mag{font-size: 38*@rem;font-weight: bold; margin: 44*@rem 0 80*@rem 0;
					.dear{  text-align: center; display: block;font-weight: bold;}
					.test{ text-align: center; display: block;font-weight: bold;}
				}
				.gauge{font-size: 40*@rem; 
					font-weight: bold; padding: 20*@rem 0;
					margin: 0 auto;
					border-radius: 10*@rem;
					background: #ff278d;
					color: #fff; 
					margin-bottom: 42*@rem; 
					text-align: center; width: 360*@rem; }
			}
		}
	}
</style>



